.image {
    width: 80px;
    height: 60px;
    display: flex;
    justify-content: flex-start;
    margin-left: -10px;
}
.pagination {
    display: flex;
    flex-direction: row;
    gap: 20px;
    font: 18px/24px "Times New Roman";
    color: #333;
    max-width: 1000px;
}
.pagination button {
    cursor: pointer;
}
.selected {
    font-weight: bold;
    color: #ccc;
}